@import url(https://fonts.googleapis.com/css?family=Barlow:400,600&display=swap);
.header {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0px;
  margin-top: 20px;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16); }
  .header-title {
    display: flex;
    flex-direction: column;
    text-align: center;
    font-size: 48px; }
    .header-title span {
      color: #F74040; }
      .header-title span:last-child {
        font-size: 36px;
        color: #05A8FA; }

.footer {
  background: #6D6767;
  color: #fff;
  box-shadow: 3px -6px 6px rgba(0, 0, 0, 0.16);
  padding: 20px;
  font-size: 20px; }
  .footer-fech {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 40px; }
  .footer-container {
    margin-left: 40px; }

body {
  font-family: 'Barlow';
  font-weight: normal; }

.time-conatiner {
  display: flex;
  justify-content: center;
  padding: 40px; }
  .time-conatiner img {
    width: 100%; }

.TimeItem {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  -webkit-transition: 0.3s;
  transition: 0.3s;
  padding: 15px 16px;
  background-color: #BBEBFF;
  border-radius: 5px; }

.TimeItem:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2); }

.titulo {
  font-size: 20px;
  font-weight: bold; }

